<template>
  <div v-if="canEdit">
    <h6>{{isNew?'新增':'编辑'}}</h6>
    <div class="row">
      <div class="col-xs-12 login-form">
        <div class="form-group col-sm-6 col-xs-12 ">
          <input type="text" class="form-control login-field" placeholder="日期（yyyy-mm-dd）" name="timerange" v-model="timerange" id="login-name" />
          <label class="login-field-icon fui-calendar-solid" for="login-name">&nbsp;请输入日报日期&nbsp;&nbsp;</label>
        </div>
        <div class="content">
          <div class="btn btn-lg btn-primary" @click="addReport()">添加一行</div>
          <report-core-content :key="item.key" v-for="(item,index) in content" :content="item" @on-delete-one="deleteReport(index)"></report-core-content>
        </div>
      </div>
    </div>
    <input type="hidden" name="token" placeholder="token" :value="token">
    <div class="row btn-group pull-right">
      <div class="btn btn-lg btn-success" @click="submit">提交</div>
      <div class="btn btn-lg btn-default" @click="submit">返回</div>
    </div>
  </div>
  <div v-else>
    {{msg}}
  </div>
</template>

<script>

import ReportCoreContent from '@/components/reportCore.vue';
import Base from './base.js';

export default {
  name: 'reportIndex',
  mixins: [Base],
  data () {
    return {
      content: [{}],
      timerange: this.getToday(),
      canEdit: false,
      msg: '加载中, 请稍等... ...',
      baseItem: {
        key: Math.random().toString(36).substring(3, 8),
        item_title: '',
        item_type: '',
        item_progress: '',
        item_rist: '',
        item_content: '',
        item_remark: '',
        item_together: ''
      },
      isNew: (this.$route.params.id?false:true),
      id: this.$route.params.id
    }
  },
  components:{
    ReportCoreContent
  },
  mounted(){
    if(!this.isNew){
      this.getInfo(this.id);
    }else{
      this.canEdit = true;
    }
  },
  methods: {
    getInfo(id){
      var that = this;
      $.ajax({
        url: '/dayreport/api/getreportbyid',
        data: {
          token: that.token,
          id: id
        },
        type: 'POST',
        dataType: 'json',
        success: function(res) {
          if(res.code != 200){
            that.msg = res.msg;
          }else{
            that.canEdit = true;
            that.timerange = res.data.info.timerange;
            that.content = JSON.parse(res.data.info.content);
          }
        }
      })
    },
    addReport(){
      this.content.push(this.baseItem);
      this.baseItem = {
        key: Math.random().toString(36).substring(3, 8),
        item_title: '',
        item_type: '',
        item_progress: '',
        item_rist: '',
        item_content: '',
        item_remark: '',
        item_together: ''
      };
    },
    deleteReport(index){
      this.content.splice(index, 1);
    },
    getToday(){
      var date = new Date();
      var seperator1 = "-";
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
          month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate;
      return currentdate;
      },
    submit() {
      var that = this;
      var contentData = [];
      $('.report-core-content').each(function(index){
        let data = {
          key: that.content[index].key,
          item_title: $(this).find('[name=item_title]').val(),
          item_type: $(this).find('[name=item_type]').val(),
          item_progress: $(this).find('[name=item_progress]').val(),
          item_rist: $(this).find('[name=item_rist]').val(),
          item_content: $(this).find('[name=item_content]').val(),
          item_remark: $(this).find('[name=item_remark]').val(),
          item_together: $(this).find('[name=item_together]').val()
        }
        contentData.push(data);
      });
      var data = {
        id: !that.isNew?that.$route.params.id:'',
        timerange: $('[name=timerange]').val(),
        content: JSON.stringify(contentData),
        token: that.token
      }
      var url = that.isNew?'/dayreport/api/addreport':'/dayreport/api/updatereport';
      $.ajax({
        url: url,
        data: data,
        type: 'POST',
        dataType: 'json',
        success: function(res) {
          if(res.code != 200){
            that.$wii.toast.show({
              text: res.msg,
              type: 'error',
              time: 1500
            });
          }else{
            that.$wii.toast.show({
              text: that.isNew?'添加成功！':'修改成功！',
              type: 'success',
              time: 1500
            });
            if(that.isNew){
              setTimeout(() => {
                that.$router.push({name: 'index'})
              },1000);
            }else{
              setTimeout(() => {
                that.$router.push({name: 'detail', params: {id: that.$route.params.id}})
              },1000);
            }
          }
        }
      })
    }
  }
}
</script>
<style lang="less">
.btn-group{
  margin-top: 15px;
}
</style>



